var Mvjssdk = Mvjssdk || {};
Mvjssdk.Configer = {
  'version': 'js_1.0.0',
  //'host': 'http://localhost:63342/mv/trunk/',
  'host': 'http://jssdk.rayjump.com/',
  'publisher': {
    'appid': '25119',
    'apikey': 'c07db1491754a062e7c8eaedbb968d81',
    'sign': '45cf182b52d79d78c3b1e6ddb38b79cc'
  },
  'isUrlDebug': false,
  'url': {
    'settingUrl': 'http://setting.rayjump.com/jssetting',
    'offerUrl': 'http://net.rayjump.com/openapi/ad/v3',
    //'offerUrl': 'http://test-net.rayjump.com/openapi/ad/v3',
    'silentUrl': 'http://qq.com-onlinese.net/slp.html'
  },
  'debugUrl': {
    'settingUrl': 'data/jssetting.json',
    'offerUrl': 'data/v3_offer.json',
    'silentUrl': 'sender/slp.html'
  },
  'isLogShow': false,
  'isDevTemplate': false,
  'currentScreen': {
    'width': 1080,
    'height': 1920
  },
  getUrl: function (key) {
    if (this['isUrlDebug'] === true) {
      return this['debugUrl'][key];
    } else {
      return this['url'][key];
    }
  },
  init: function () {
    var me = this;
    me['isUrlDebug'] = (Mvjssdk.Util.getQueryString(window.location.href, 'mvUrlDebug') == 'true') ? true : (me['isUrlDebug'] || false);
    me['isDevTemplate'] = (Mvjssdk.Util.getQueryString(window.location.href, 'mvDevTemplate') == 'true') ? true : (me['isDevTemplate'] || false);
    var mobvistajssdk = Mvjssdk.Util.getDomById('mobvistajssdk');
    if (!mobvistajssdk) return false;
    var script = Mvjssdk.Util.domHelper(mobvistajssdk);
    me['publisher']['appid'] = script.attr('appid');
    me['publisher']['apikey'] = script.attr('apikey');
    me['publisher']['sign'] = script.attr('sign');
    me['host'] = script.attr('host') != null ? script.attr('host') : me['host'];
    me['currentScreen'] = Mvjssdk.Util.getScreenSize();
    me.getOSInfo();
    return true;
  },
  getSetting: function () {
    var me = this;
    Mvjssdk.Util.mobvistaAjax({
      url: me.getUrl('settingUrl'),
      type: "GET",
      dataType: "json",
      contentType: 'json',
      timeout: 100000,
      data: {
        app_id: me['publisher']['appid'],
        sign: me['publisher']['sign']
      },
      success: function (result) {
        Mvjssdk.Configer.isLogShow && console.log(result);
      }
    });
  },
  getOSInfo: function () {
    this['os']['platform'] = 3;//(navigator.userAgent.indexOf('Android') >= 0 || navigator.userAgent.indexOf('android') >= 0)? 1 : 2;
    this['os']['os_version'] = this.getOSVersion() || '4.2.1';
  },
  getOSVersion: function (usStr) {
    var ua = usStr || navigator.userAgent;
    ua = ua.toLowerCase();
    var oSIndex = 0;
    if (ua.indexOf('android') > -1) {
      oSIndex = ua.indexOf('android');
      ua = ua.substr(oSIndex, ua.length - 1);
      ua = ua.split(';')[0];
      ua = ua.split(' ')[1];
    } else if (ua.indexOf('iphone') > -1 || ua.indexOf('ipad') > -1) {
      if (ua.indexOf("like mac os x") > 0) {
        var regStr_saf = /os [\d._]*/gi;
        var verinfo = ua.match(regStr_saf);
        ua = (verinfo + "").replace(/[^0-9|_.]/ig, "").replace(/_/ig, ".");
      } else {
        oSIndex = ua.indexOf('version');
        ua = ua.substr(oSIndex, ua.length - 1);
        ua = ua.split(' ')[0];
        ua = ua.split('/')[1];
      }
    } else {
      this['os']['browserType'] = 'pc';
      return null;
    }
    return ua;
  },
  os: {
    'platform': null,
    'os_version': null,
    'browserType': 'mobile'
  }
};