var Mvjssdk = Mvjssdk || {};

Mvjssdk.Util.addListener(document,'DOMContentLoaded',function(){
	var ua = window.navigator.userAgent.toLowerCase();
	if(ua.indexOf('googlebot')>=0||ua.indexOf('search console')>=0) return;
	Mvjssdk.Logger.init();
	if(Mvjssdk.Configer.init()){
		Mvjssdk.Offer.init();
	}
});