var Mvjssdk = Mvjssdk || {};
Mvjssdk.Logger = {
	debug: true,
	uploadLog: true,
	postType:'GA',
	init: function() {

		this['debug'] = (Mvjssdk.Util.getQueryString(window.location.href, 'mvLogDebug') == 'true') ? true : (Mvjssdk.Configer['isLogShow'] || false);
		// if(this.debug==false) console.log = function(){};
	},
	post: function(param) {
		var me = this;
		if (this.uploadLog == true) {
			var postObj = me['loggerSet'][param['postType']]? me['loggerSet'][param['postType']] : me['loggerSet'][me['postType']];
			postObj.doPost(param);
		}
	},
	loggerSet: {
		'GA': {
			hasInit: false,
			checkEnable: true,
			GAId: 'UA-62639739-1',
			_loadGA: function(callback) {
				var me = this;
				(function(i, s, o, g, r, a, m) {
					i['GoogleAnalyticsObject'] = r;
					i[r] = i[r] || function() {
						(i[r].q = i[r].q || []).push(arguments)
					}, i[r].l = 1 * new Date();
					a = s.createElement(o),
						m = s.getElementsByTagName(o)[0];
					a.async = 1;
					a.src = g;
					m.parentNode.insertBefore(a, m)
				})(window, document, 'script', '//www.google-analytics.com/analytics.js', 'ga');

				ga('create', me.GAId, 'auto');
				ga('send', 'pageview');
				me.hasInit = true;
				if (callback) callback();
			},
			doPost: function(param) {
				var me = this;
				if (me.hasInit == true) {
					me._doPost(param);
				} else {
					me._loadGA(function() {
						me._doPost(param);
					});
				}
			},
			_doPost: function(param) {
				var me = this;
				var eventValue = parseInt(param['eventValue']);
				if(isNaN(eventValue)){
					ga('send', 'event', 'JSSDK_'+param['eventType'], param['eventDesc'], eventValue);
				}else{
					ga('send', 'event', 'JSSDK_'+param['eventType'], param['eventDesc']);
				}
			}
		}
	}
};